<template>
  <div>

    <!-- Update Assignment Modal -->
  <div
        class="modal fade"
        id="UpdateAssignmentModal"
        tabindex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title text-center" id="exampleModalLabel">
                Update Assignment
              </h5>
              <button
                type="button"
                class="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <!-- modal body -->
            <div class="modal-body">
              <div class="card-body">
                <form @submit.prevent="UpdateAssignment">
                  <div class="form-group">
                    <label for="acode">Assignment Code</label>
                    <!-- <input
                      type="text"
                      id="acode"
                      name="acode"
                      placeholder="Enter Assignment Code (A Code)"
                      v-model="acode"
                      class="form-control"
                    /> -->
                
                  </div>
                   <div class="form-group">
                    <p>{{NewAcode}}</p>
                  </div> 
                  <div class="form-group">
                    <label for="aheading">Heading</label>
                    <input
                      type="text"
                      id="aheading"
                      name="aheading"
                      placeholder="Enter Assignment Heading"
                      v-model="NewAssignmenthead"
                      class="form-control"
                    />
                  </div>
                  <div class="form-group">
                    <label for="adesc">Description</label>
                    <input
                      type="text"
                      id="adesc"
                      name="adesc"
                      placeholder="Enter description"
                      required
                      v-model="NewAssignmentdesc"
                      class="form-control"
                                      data-bs-dismiss="modal"
                    />
                  </div>
                  <div class="form-group">
                    <label for="adesc">Assignment Link</label>
                    <input
                      type="text"
                      id="adesc"
                      name="adesc"
                      placeholder="Enter description"
                      v-model="NewAssignmentlink"
                      class="form-control"
                      required
                    />
                  </div>

                  <button class="btn btn-success mt-2 " data-bs-dismiss="modal">Update Assignment</button>
                </form>
              </div>
            </div>
            <div class="modal-footer">
              <button
                type="button"
                class="btn btn-secondary"
                data-bs-dismiss="modal"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>

  <!-- Add Assignment Modal -->
  <div
        class="modal fade"
        id="AddAssignmentModal"
        tabindex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title text-center" id="exampleModalLabel">
                Add Assignment
              </h5>
              <button
                type="button"
                class="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <!-- modal body -->
            <div class="modal-body">
              <div class="card-body">
                <form @submit.prevent="AddAssignment">
                  <div class="form-group">
                    <label for="acode">Assignment Code <small>(It Should be Unique)</small></label>
                    <input
                      type="text"
                      id="acode"
                      name="acode"
                      placeholder="Enter Assignment Code (A Code)"
                      v-model="acode"
                      class="form-control"
                      required
                    />
                  </div>
                  <div class="form-group">
                    <label for="aheading">Heading</label>
                    <input
                      type="text"
                      id="aheading"
                      name="aheading"
                      placeholder="Enter Assignment Heading"
                      v-model="aheading"
                      class="form-control"
                      required
                    />
                  </div>
                  <div class="form-group">
                    <label for="adesc">Description</label>
                    <input
                      type="text"
                      id="adesc"
                      name="adesc"
                      placeholder="Enter description"
                      v-model="adesc"
                      class="form-control"
                      required
                    />
                  </div>
                  <div class="form-group">
                    <label for="alink">Assignment Link (Github / Drive) link</label>
                    <input
                      type="text"
                      id="alink"
                      name="alink"
                      placeholder="Enter description"
                      v-model="alink"
                      class="form-control"
                      required
                    />
                  </div>

                  <button class="btn btn-success mt-2 " data-bs-dismiss="modal"
>Add </button>
                    <button class="btn btn-success mt-2 ms-2" type="reset">Clear</button>
                </form>
              </div>
            </div>
            <div class="modal-footer">
              <button
                type="button"
                class="btn btn-secondary"
                data-bs-dismiss="modal"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>

<button
      class="btn btn-success mt-5"
      data-bs-toggle="modal"
      data-bs-target="#AddAssignmentModal"
    >
      Add Assignment
    </button>
  
<div class="mt-2">
<div class="table-responsive mt-4">
    <table class="table table-striped table-sm">
        <thead>
            <tr class="text-center fw-bold">
            <th scope="col">A Code</th>
            <th scope="col">Assignment Heading</th>
            <th scope="col">Assignment Desc</th>
            <th scope="col">Assignment Link</th>
            <th scope="col">Update</th>
            <th scope="col">Delete</th>
            </tr>
            </thead>
            <tbody class="text-center ">
                <tr class="mt-2" v-for="(assignment, _id) in Assignments" :key="_id">

                <td>{{assignment.acode}}</td>
                <td>{{assignment.aheading}}</td>
                <td>{{assignment.adesc}}</td>
                <td><a :href="assignment.alink" target="_blank"><button class="btn btn-info">Open Link</button></a></td>

                <td><button class="btn btn-primary"  data-bs-toggle="modal"
      data-bs-target="#UpdateAssignmentModal" @click="editAssignment(_id)">Update</button></td>
                <td><button class="btn btn-danger" @click="deleteAssignment(_id)">Delete</button></td>
                
                </tr>
                
            </tbody>
    </table>
</div>
</div>


</div>

</template>

<script>
import axios from "axios";
import config from "@/config"


export default {
name:'AddAssignment',
data(){
return {
     acode:"",
    aheading: "",
    adesc:"",
  alink:"" ,

//assignments array
  Assignments:[],

  //update form data
  NewAcode:'',
  NewAssignmenthead:'',
  NewAssignmentdesc:'',
  NewAssignmentlink:''
      
      }

},
methods:{
    // Add Assignment
async AddAssignment(){

let assignment = {
        acode: this.acode,
        aheading: this.aheading,
        adesc: this.adesc,
        alink: this.alink
      }
let res = await axios.post(`${config.apiBaseUrl}/api/assignments/add-assignment`,assignment)
      
      if(res.data.success){
                this.$toasted.show('Assignment Added');
      this.getAssignments()
        }
        // return res;
},


// get assignment lists
async getAssignments(){
  
    let {
    data
  } = await axios.get(`${config.apiBaseUrl}/api/assignments/get-assignments`)
  this.Assignments = data
 

},

// Delete Assignment
     async deleteAssignment(_id){
      
       let id  = this.Assignments[_id]._id
      let res = await axios.delete(`${config.apiBaseUrl}/api/assignments/delete-assignment`,{data: { _id:id} })
  
  this.getAssignments() //calling get student
        this.$toasted.show('Assignment Deleted');
      if(res.data.success){
          console.log('add something here')
        }
        return res;
     },

editAssignment(_id){
  this._id=this.Assignments[_id]._id
  this.NewAcode = this.Assignments[_id].acode
  this.NewAssignmenthead = this.Assignments[_id].aheading
  this.NewAssignmentdesc = this.Assignments[_id].adesc
  this.NewAssignmentlink = this.Assignments[_id].alink
},


//update assignment
async UpdateAssignment(){
  let assignment = {
    _id:this._id,
    aheading: this.NewAssignmenthead,
    adesc: this.NewAssignmentdesc,
    alink: this.NewAssignmentlink
  }

let res = await axios.patch(`${config.apiBaseUrl}/api/assignments/update-assignment`,assignment)
      this.getAssignments() //calling gets student
                this.$toasted.show('Assignment updated');

      if(res.data.success){
          console.log('add something here')
        }
        return res;


} 

},
created(){
  this.getAssignments()
}
}
</script>

<style scoped>
label {
  font-weight: 600 !important;
  margin: 0.5em 0em 0.5em 0em;
}
a{
  color: white;
}


</style>